@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");

* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

html {
  width: 100%;
  height: 100%;
}

body {
  display: flex;
  overflow: hidden;
  flex-direction: column;
}

.navbar {
  height: 60px;
}

.wrapper {
  height: calc(100vh - 60px);
  max-height: calc(100vh - 60px);
  margin: 0 auto;
  max-width: 100vw;
  /* overflow: hidden; */
  text-align: center;
  scroll-behavior: smooth;
}

#images-wrapper {
  /* position: relative; */
  height: 100%;
  overflow: hidden;
}

.waves {
  /* position: absolute; */
  /* bottom: 0px; */
  /* height: 60px; */
  width: 100%;

  svg {
    position: absolute;
    left: 0;
    bottom: 0;

    width: 100%;
  }

  path {
    height: 60px;
    width: 100%;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0; /* Start with opacity 0 */
  }
  to {
    opacity: 1; /* Fade in to opacity 1 */
  }
}

img {
  width: 100%;
  max-width: 100%;
  object-fit: cover;
  animation: fadeIn 0.5s ease-in-out;
  height: 100%;
}

#images-wrapper::-webkit-scrollbar {
  width: 0px;
}

@media only screen and (max-width: 768px) {
  .wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;

    /* height: calc(100dvh - 60px); */
    max-height: calc(100dvh - 60px);
    /* height: -webkit-fill-available; */
  }

  img {
    height: -webkit-fill-available;
  }
}
