.navbar {
  display: flex;
  align-items: center;
  justify-content: center;

  height: 60px;
  padding-inline: 24px;
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(9, 9, 121, 1) 35%,
    rgba(0, 44, 255, 1) 100%
  );

  .title {
    color: white;
    font-weight: 600;
    font-size: 1.75rem;
    letter-spacing: 1px;
  }
}
